@import url('https://fonts.googleapis.com/css?family=Pacifico|Sorts+Mill+Goudy|Lora');

.Canvas {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 1;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: distribute;
      align-content: space-around;

}

.background {
  background: red; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(left top, #18FFFF, white); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(bottom right, #18FFFF, white); /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
  background: -webkit-gradient(linear, left top, right bottom, from(#18FFFF), to(white));
  background: -webkit-linear-gradient(top left, #18FFFF, white);
  background: -o-linear-gradient(top left, #18FFFF, white);
  background: linear-gradient(to bottom right, #18FFFF, white); /* Standard syntax */
}


.AppHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.header {
  font-size: 45pt;
  color: white;
}

.lowerHeader {
  padding-left: 70%;
  font-size: 8pt;
}

.TextField {
  margin-left:5px;
  margin-right:5px;
}

.search {
  margin-bottom: 5px;
}

.DialogueBox {
  height: 70%;
  width: 70%;
  background: #eaecef;
  overflow-y: scroll;
  padding-top: 20px;
}

.ChatBubbleHolder {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin: 20px;
}

.flex-right {
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.flex-left {
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.ChatBubble {
  max-width: 40%;
  min-width: 30px;
  font-size: 20px;
  text-align: left;
  font-family: 'Lora', serif;
  padding:10px 10px 10px 10px;
}

.user {
  background-color: #e8ebef;
  border-radius: 20px 0 20px 20px;
}

.bot {
  background-color: #52dcf2;
  color: white;
  border-radius: 20px 20px 20px 0;
}

.WeatherIcon {
  width: 15%;
  padding-left: 5%;
  vertical-align: middle;
}

.list {
  list-style-type: none;
  text-align: left;
  padding-left: 20%;
}

.list li {
  padding-top: 5px;
}

.Pacifico {
  font-family: Pacifico,cursive;
}

.Sorts-Mill-Goudy{
  font-family: 'Sorts Mill Goudy', serif;
}

.Lora {
  font-family: 'Lora', serif;
}



